<template>
  <div class="body">
    <el-card class="box-card">
      <el-row :gutter="20" style="margin-top:5px;">
        <el-col :span="6">
          <div class="bar">
            <div class="title" ></div>
            <el-select v-model="searchOptions.searchParams.customerValue" @change="customerNameSelectionChange()" clearable>
              <el-option
                v-for="item in searchOptions.options.customerNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.brandValue" @change="brandSelectionChange()" clearable>
              <el-option
                v-for="item in searchOptions.options.brandNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </div>
        </el-col>
        
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
              <el-select v-model="searchOptions.searchParams.rangeValue" @change="rangeSelectionChange()" clearable>
              <el-option
                v-for="item in searchOptions.options.rangeNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.idValue" clearable>
              <el-option
                v-for="item in searchOptions.options.styleGroupNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </div>
        </el-col>
        
      </el-row>
      <el-row :gutter="20" style="margin-top: 30px; margin-bottom: 5px;">
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.clothingLevelValue" clearable>
              <el-option
                v-for="item in searchOptions.options.clothingTypeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="bar">
            <div class="title"></div>
            <el-date-picker
              class="inputBar"
              v-model='searchOptions.searchParams.dateRange'
              type="daterange"
              range-separator=""
              start-placeholde=""
              end-placeholde=""
              clearable>
            </el-date-picker>
          </div>
        </el-col>
        <el-col :span="2">
            <el-button type="primary" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <el-card class="box-card">
      <div class="table">
        <el-row :gutter="10">
          <el-col :span="4">
            <el-button type="primary" @click="addStyleGroup"></el-button>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="deleteStyleGroup"></el-button>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="unbindStyleGroup"> </el-button>
          </el-col>
        </el-row>
        <el-table
          :data="tableDataA"
          max-height="400"
          border
          @selection-change="changeCheckBoxFun"
          :stripe="true"
          :highlight-current-row="true"
          style="width: 100%; margin-top: 20px">
          <el-table-column type="selection" width="50" align="center"></el-table-column>
          <el-table-column type="index" label="" width="50" align="center"></el-table-column>
          <el-table-column prop="number" width="150" label="" align="center"></el-table-column>
          <el-table-column prop="name" width="150" label="" align="center"></el-table-column>
          <el-table-column prop="rangeNumber" width="130" label="" align="center"></el-table-column>
          <el-table-column prop="customerName" width="120" label="" align="center"></el-table-column>
          <el-table-column prop="rangeName" width="150" label="" align="center"></el-table-column>
          <el-table-column prop="brandName" label="" align="center"></el-table-column>
          <el-table-column prop="clothingLevelName" label="" align="center"></el-table-column>
          <el-table-column prop="createrName" label="" align="center"></el-table-column>
          <el-table-column prop="deptName" label="" align="center"></el-table-column>
          <el-table-column prop="stateName" width="70" label="" align="center"></el-table-column>
          <el-table-column prop="createTime" width="170" label="" align="center"></el-table-column>
          <el-table-column label="" width="150" min-width="100" align="center" fixed="right">
            <template slot-scope="scope">
              <!-- <el-button @click="getStyleGroupData(scope.row)" type="text" size="small"></el-button> -->
              <el-button @click="changeStyleGroupData(scope.row)" type="text" size="small"></el-button>
              <el-button @click="deleteStyleGroupRow(scope.row)" type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="pagination.currentPage"
            :page-sizes="pagination.pageSizes"
            :page-size="pagination.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total"
          ></el-pagination>
        </div>
      </div>
    </el-card>

    <el-dialog  :modal="false" title="" :visible.sync="dialogFormVisible">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-row :gutter="20" style="margin-top:5px;">
          <el-col :span="8">
            <el-form-item label="" prop="tmpCustomer" placeholder="">
              <el-select v-model="ruleForm.tmpCustomer" @change="dialogCustomerNameSelectionChange()" >
                <el-option
                  v-for="item in options.customerNameOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item> 
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="tmpBrand" placeholder="">
              <el-select v-model="ruleForm.tmpBrand" @change="dialogBrandSelectionChange()">
                <el-option
                  v-for="item in options.brandNameOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item> 
          </el-col>
          <!-- <el-col :span="8">
            <el-form-item label="" prop="clothingLevelName" placeholder="">
              <el-select v-model="ruleForm.clothingLevelName" >
                <el-option
                  v-for="item in options.clothingTypeOptions"
                  :key="item.id"
                  :label="item.clothingLevelName"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item> 
          </el-col> -->
        </el-row>
        <el-row :gutter="20" style="margin-top: 30px; margin-bottom: 5px;">
          <el-col :span="8">
            <el-form-item label="" prop="tmpRange" placeholder="">
              <el-select v-model="ruleForm.tmpRange" >
                <el-option
                  v-for="item in options.rangeNameOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item> 
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="tmpStyleGroup" placeholder="">
              <el-input v-model="ruleForm.tmpStyleGroup" clearable placeholder=""></el-input>
            </el-form-item> 
          </el-col>
        </el-row>
        <el-row style="margin: 50px 0 10px 0">
          <el-col :span="3" :offset="10">
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          </el-col>
          <el-col :span="3">
            <el-button type="info" @click="cancel"></el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import styleManagementVue from '../style/styleManagement.vue';
import { error } from 'util';
export default {
  data() {
    return {
      pagination: {
        currentPage: 1,
        pageSizes: [2, 10, 20, 30, 50],
        pageSize: 2,
        total: 0,
      },
      tableDataA:[],
      searchOptions:{
        searchParams :{
          customerName: "",
          brandName: "",
          clothingLevelName: "",
          rangeName: "",
          name: "",
          dateRange: "", 
        },
        options: {
          customerNameOptions: [],
          brandNameOptions: [],
          clothingTypeOptions: [],
          rangeNameOptions: [],
          styleGroupNameOptions:[],
        }
      },
      data:{
        tableData:[]
      },
      multipleSelection: [],
      dialogFormVisible: false,
      rules: {
        tmpCustomer: [
          { required: true, message: '', trigger: 'change' }
        ],
        tmpBrand: [
          { required: true, message: '', trigger: 'change' }
        ],
        clothingLevelName: [
          { required: true, message: '', trigger: 'change' }
        ],
        tmpRange: [
          { required: true, message: '', trigger: 'change' },
        ],
        tmpStyleGroup: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      ruleForm: {
        tmpCustomer:"",
        tmpBrand:"",
        tmpRange:"",
        tmpStyleGroup:"",

        id: "",
        number: "",
        name: "",
        rangeNumber:"" ,
        rangeId: "",
        rangeName:"" ,
        customerName:"" ,
        customerId:"",
        brandName: "",
        brandId: "",
        clothingLevelName:"" ,
        clothingLevelId:"",
        createrName: "",
        deptName: "",
        createTime: "",
        state:"",
        havePlan:"",
      },
      options: {
        customerNameOptions: [],
        brandNameOptions: [],
        clothingTypeOptions: [],
        rangeNameOptions: [],
      },
      controlData: {
        ifStyleGroupAdd: false,
        ifStyleGroupChange: false,
      },
    };
  },
  created: function () {
    const that = this;
    console.log('');

    //
    this.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getCustomerName`)
      .then(response => {
        console.log("getCustomer ");
        var resData = response.data;
        resData.forEach(element => {
          this.searchOptions.options.customerNameOptions.push({
            id:element.id,
            name:element.name,
          });
          this.options.customerNameOptions = this.searchOptions.options.customerNameOptions;
        });
      })
      .catch(error => {
        console.log("getCustomer error!");
        this.searchOptions.options.customerNameOptions = [
          {
            id: 42453,
            name: "A"
          },
          {
            id: 41526,
            name: "B"
          },
        ];
        this.options.customerNameOptions = this.searchOptions.options.customerNameOptions;
      });

    //
    that.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getClothingLevelName`)
      .then(response => {
        this.searchOptions.options.clothingTypeOptions = response.data;
      })
      .catch(error => {
        console.log("");
      });

    //
    var param = {
      customerId:"",
      brandId:"",
      rangeId:"",
      clothingLevelId:"",
      id:"",
      startDate:"",
      endDate:""
    };
    this.$axios
      .post(`${window.$config.HOST}/infoManagement/getStyleGroupList`,param)
      .then(response => {
        this.data.tableData = response.data;
        this.data.tableData.forEach(element=>{
          if(element.state===1) element.stateName="";
          else if(element.state===2) element.stateName="";
          else if(element.state===3) element.stateName="";
          else if(element.state===4) element.stateName="";
          else if(element.state===5) element.stateName="";
          else if(element.state===6) element.stateName="";
          var newDate = new Date(element.createTime);
          element.createTime = newDate.toLocaleString();


                  this.pagination.total=response.data.length;
        let i = (this.pagination.currentPage-1) * this.pagination.pageSize;
        let k = (this.pagination.currentPage-1) * this.pagination.pageSize;
        this.tableDataA=[];
        
        for(;i-k<this.pagination.pageSize&&i<this.data.tableData.length;i++)
        {
          this.tableDataA.push(this.data.tableData[i]);
        }
        });
      })
      .catch(error => {
        console.log("")
      });
 
    //
    this.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getBrandName`,{
        params:{customerId:""}
      })
      .then(response => {
          this.searchOptions.options.brandNameOptions = response.data;
          // this.options.brandNameOptions = this.searchOptions.options.brandNameOptions;
      })
      .catch(error => {
        console.log("");
        this.searchOptions.options.brandNameOptions = [
          {
            id: 1,
            name: "X"
          },
          {
            id: 2,
            name: "Y"
          },
        ];
        // this.options.brandNameOptions = this.searchOptions.options.brandNameOptions;
      });

    //
    this.$axios
      .get(`${window.$config.HOST}/infoManagement/getRangeName`,{
        params:{brandId:""}
      })
      .then(response => {
        this.searchOptions.options.rangeNameOptions = response.data;
      })
      .catch(error => {
        console.log("");
        /* this.searchOptions.options.rangeNameOptions = [
          {
            id: 1,
            name: "Fall-2019(07/08/09)"
          },
          {
            id: 2,
            name: "Spring-2019(01/02/03)"
          },
          {
            id: 3,
            name: "Winter-2019(10/11/12)"
          },
        ]; */
        // this.options.rangeNameOptions = this.searchOptions.options.rangeNameOptions;
      });
    
    //
    this.$axios
      .get(`${window.$config.HOST}/infoManagement/getStyleGroupName`,{
        params:{rangeId:""}
      })
      .then(response => {
        if(response.data.errcode < 0){
          console.log("");
        }else{
          response.data.forEach(element=>{
            this.searchOptions.options.styleGroupNameOptions.push({
              id:element.name,
              name:element.name
            });
          });
        }
      })
      .catch(error => {
        console.log("");
        this.searchOptions.options.styleGroupNameOptions = [
          {
            id: 5451,
            name: "1"
          },
          {
            id: 27452,
            name: "2"
          },
          {
            id: 356,
            name: "3"
          },
        ];
      });
  },
  methods: {
    //get

       handleSizeChange(val) {
    
        this.pagination.pageSize=val;
        console.log("+"+this.pagination.pageSize)
        this.handleSearch();
      },
      handleCurrentChange(val) {
        this.pagination.currentPage=val;
         this.handleSearch();
      },
    rangeSelectionChange(){
      console.log("");
      console.log(this.searchOptions.searchParams.rangeValue);
      // var param = {
      //   brandId: this.searchOptions.searchParams.rangeValue,
      // };
      
    },
    //get
    brandSelectionChange(){
      console.log("");
      console.log(this.searchOptions.searchParams.brandValue);
      // var param = {
      //   brandId: this.searchOptions.searchParams.brandValue,
      // };
      
    },
    //get
    dialogBrandSelectionChange(){
      // consol.log(val);
      console.log("");
      console.log(this.ruleForm.tmpBrand);
      this.ruleForm.tmpRange = "";
      var param = {
        brandId: (this.ruleForm.brandName===this.ruleForm.tmpBrand)?this.ruleForm.brandId:this.ruleForm.tmpBrand,
      };
      console.log(param);
      this.options.rangeNameOptions = [];
      //
      this.$axios
        .get(`${window.$config.HOST}/infoManagement/getRangeName`,{
          params:param
        })
        .then(response => {
          this.options.rangeNameOptions = response.data;
          // this.options.rangeNameOptions = this.searchOptions.options.rangeNameOptions;
        })
        .catch(error => {
          console.log("");
          // this.options.rangeNameOptions = this.searchOptions.options.rangeNameOptions;
        });
      
    },
    //get
    customerNameSelectionChange(){
      // consol.log(val);
      console.log("");
      console.log(this.searchOptions.searchParams.customerValue);
      
    },
    //get
    dialogCustomerNameSelectionChange(){
      // consol.log(val);
      this.ruleForm.tmpBrand = "";
      this.ruleForm.tmpRange = "";
      console.log("");
      var param = {
        customerId: (this.ruleForm.tmpCustomer===this.ruleForm.customerName)?this.ruleForm.customerId:this.ruleForm.tmpCustomer,
      };
      console.log(param);
      //
      this.$axios
        .get(`${window.$config.HOST}/baseInfoManagement/getBrandName`,{
          params:param
        })
        .then(response => {
          if(response.data < 0){
            console.log("");
          }else{
            response.data.forEach(element=>{
              this.options.brandNameOptions = response.data;
            });
          }
        })
        .catch(error => {
          console.log("");
          this.options.brandNameOptions = [
            {
              id: 1,
              name: "X"
            },
            {
              id: 2,
              name: "Y"
            },
          ];
        });

    },
    // 
    changeDate(date) {
      console.log(date);
      if(!date){
        return "";
      }else{
        // var date = new Date(date);
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        var d = date.getDate();
        d = d < 10 ? "0" + d : d;
        // var h = date.getHours();
        // var minute = date.getMinutes();
        // minute = minute < 10 ? "0" + minute : minute;
        // var second = date.getSeconds();
        // second = minute < 10 ? "0" + second : second;
        // return y + "-" + m + "-" + d + " " + h + ":" + minute + ":" + second;
        return y + "-" + m + "-" + d ;
      }
    },

    // 
    changeCheckBoxFun(val){
      const that = this;
      that.multipleSelection = val;
      console.log("changeCheckBox");
      console.log(that.multipleSelection);
      console.log("changeCheckBox",that.multipleSelection.length);
    },
    // 
    collectSearchOptions(){ 
      const that = this;
      let searchCondition = {};
      for (let key in that.searchOptions.searchParams){
        if (that.searchOptions.searchParams[key] !== "") {
          if (key == "dateRange"){
            var dateRange = that.searchOptions.searchParams[key];
            var DateStart = that.changeDate(dateRange[0]);
            searchCondition["DateStart"] = DateStart;
            var DateEnd = that.changeDate(dateRange[1]);
            searchCondition["DateEnd"] = DateEnd;
          }
          else{
            searchCondition[key] = that.searchOptions.searchParams[key];
          }
        }
      }
      console.log("", searchCondition);
      return searchCondition;
    },
    // 
    handleSearch(){
            let startDate;
      let endDate;
      if(this.searchOptions.searchParams.dateRange==null)
      {
        startDate="";
        endDate="";
      }
      else{
          startDate= this.changeDate(this.searchOptions.searchParams.dateRange[0]),
          endDate=this.changeDate(this.searchOptions.searchParams.dateRange[1])
      }
      console.log(''+this.searchOptions.searchParams.customerValue); 
      var param = {
        customerId:(this.searchOptions.searchParams.customerValue==="")?"":this.searchOptions.searchParams.customerValue,
        brandId:(this.searchOptions.searchParams.brandValue==="")?"":this.searchOptions.searchParams.brandValue,
        rangeId:(this.searchOptions.searchParams.rangeValue==="")?"":this.searchOptions.searchParams.rangeValue,
        clothingLevelId:(this.searchOptions.searchParams.clothingLevelValue==="")?"":this.searchOptions.searchParams.clothingLevelValue,
        id:(this.searchOptions.searchParams.idValue === "")?"":this.searchOptions.searchParams.idValue,
        startDate:startDate,
        endDate:endDate
      }
      console.log(param);
      this.$axios
        .post(`${window.$config.HOST}/infoManagement/getStyleGroupList`,param)
        .then(response=>{
          this.data.tableData = response.data;
          this.data.tableData.forEach(element=>{
            if(element.state===1) element.stateName="";
            else if(element.state===2) element.stateName="";
            else if(element.state===3) element.stateName="";
            else if(element.state===4) element.stateName="";
            else if(element.state===5) element.stateName="";
            else if(element.state===6) element.stateName="";
            var newDate = new Date(element.createTime);
            element.createTime = newDate.toLocaleString();

              this.pagination.total=response.data.length;
        let i = (this.pagination.currentPage-1) * this.pagination.pageSize;
        let k = (this.pagination.currentPage-1) * this.pagination.pageSize;
        this.tableDataA=[];
        
        for(;i-k<this.pagination.pageSize&&i<this.data.tableData.length;i++)
        {
          this.tableDataA.push(this.data.tableData[i]);
        }
          });
        })
        .catch(error=>{
          console.log("");
        });
    },
    // 
    addStyleGroup(){
      const that = this;
      console.log('');
      
      this.controlData.ifStyleGroupAdd = true;

      this.dialogFormVisible = true;
    },
    // 
    deleteStyleGroup(){
      const that = this;
      console.log('');
      if(that.multipleSelection.length ===0){
        this.$message({
          message: '',
          type: 'warning'
        });
      }else if(that.multipleSelection.length >= 1){
        console.log("" + that.multipleSelection.length + "");
        this.$confirm("" + that.multipleSelection.length + ", ?", "", {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          this.multipleSelection.forEach(element=>{
            // console.log(element);
            this.$axios
              .delete((`${window.$config.HOST}/infoManagement/deleteStyleGroup`),{
                params:{id:element.id}
              })
              .then(response=>{
                if(response.data >= 0){
                  this.$message({
                    type: 'success',
                    message: '!'
                  });
                }else{
                  this.$message.error('!'); 
                }
                this.handleSearch();
              })
              .catch(error=>{
                this.$message.error(
                    '!'
                  ); 
              });
          })
          this.$message({
            type: 'success',
            message: '!'
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      }
    },
    // 
    unbindStyleGroup(){
      const that = this;
      console.log('');
      if(that.multipleSelection.length ===0){
        this.$message({
          message: '',
          type: 'warning'
        });
      }
      else if(that.multipleSelection.length >= 1){
        // console.log("" + that.multipleSelection.length + "");
        this.$confirm("" + that.multipleSelection.length + ", ?", "", {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          this.multipleSelection.forEach(element=>{
            console.log("");
            this.$axios
              .get(`${window.$config.HOST}/infoManagement/unbindStyleGroup`,{
                params:{id:element.id}
              })
              .then(response=>{
                if(response.data < 0){
                  this.$message.error(element.name+"!")
                  console.log(element.name+"!");
                }else{
                  this.$message({
                    type: 'success',
                    message: element.styleGroupName + '!'
                  });
                }
              })
              .catch(error=>{
                console.log(element.name+"!");
                this.$message.error(element.name+"!")
              });
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      }
    },
    /* //
    getStyleGroupData(row){
      this.ruleForm.customerName = row.customerName;
      this.ruleForm.brandName = row.brandName;
      this.ruleForm.clothingLevelName = row.clothingLevelName;
      this.ruleForm.rangeName = row.rangeName;
      this.ruleForm.name = row.name;

      this.dialogFormVisible = true;

    }, */
    // 
    changeStyleGroupData(row){
      const that = this;
      console.log("");
      this.controlData.ifStyleGroupChange = true;
      
      this.ruleForm.tmpCustomer = row.customerName,
      this.ruleForm.tmpBrand = row.brandName,
      this.ruleForm.tmpRange = row.rangeName,
      this.ruleForm.tmpStyleGroup = row.name,

      //
      this.$axios
        .get(`${window.$config.HOST}/baseInfoManagement/getBrandName`,{
          params:{customerId:row.customerId}
        })
        .then(response => {
            this.options.brandNameOptions = response.data;
            // this.options.brandNameOptions = this.searchOptions.options.brandNameOptions;
        })
        .catch(error => {
          console.log("");
        });
      //
      this.$axios
        .get(`${window.$config.HOST}/infoManagement/getRangeName`,{
          params:{brandId:row.brandId}
        })
        .then(response => {
          this.options.rangeNameOptions = response.data;
          // this.options.rangeNameOptions = this.searchOptions.options.rangeNameOptions;
        })
        .catch(error => {
          console.log("");
        });
      this.options.rangeNameOptions = [];
      

      this.ruleForm.id = row.id,
      this.ruleForm.number= row.number,
      this.ruleForm.name= row.name,
      this.ruleForm.rangeNumber=row.rangeNumber,
      this.ruleForm.rangeId=row.rangeId,
      this.ruleForm.rangeName= row.rangeName,
      this.ruleForm.customerName= row.customerName,
      this.ruleForm.customerId=row.customerId,
      this.ruleForm.brandName= row.brandName,
      this.ruleForm.brandId= row.brandId,
      this.ruleForm.clothingLevelName= row.clothingLevelName,
      this.ruleForm.clothingLevelId=row.clothingLevelId,
      this.ruleForm.createrName= row.createrName,
      this.ruleForm.deptName= row.deptName,
      this.ruleForm.createTime= row.createTime,
      this.ruleForm.state=row.state,
      this.ruleForm.havePlan=row.havePlan,

      this.dialogFormVisible = true;
    },
    // 
    deleteStyleGroupRow(row){
      const that = this;
      console.log(""+row.id);
      this.$confirm("", "", {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
      .then(() => {
        this.$axios
          .delete((`${window.$config.HOST}/infoManagement/deleteStyleGroup`),{
            params:{id:row.id}
          })
          .then(response=>{
            if(response.data >= 0){
              this.$message({
                type: 'success',
                message: '!'
              });
              this.handleSearch();
            }else{
              this.$message.error('!'); 
            }
          })
          .catch(error=>{
            this.$message.error(
                '!'
              ); 
          });
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    // 
    submitForm(formName){
      const that = this;
      console.log("");

      if(this.controlData.ifStyleGroupAdd){
        this.controlData.ifStyleGroupAdd = false;
        var param = {
          customerId : (this.ruleForm.tmpCustomer === "")? null:this.ruleForm.tmpCustomer,
          brandId : (this.ruleForm.tmpBrand === "")? null:this.ruleForm.tmpBrand,
          rangeId : (this.ruleForm.tmpRange === "")? null:this.ruleForm.tmpRange, 
          name : (this.ruleForm.tmpStyleGroup === "")? null:this.ruleForm.tmpStyleGroup,
        };
        console.log(param)
        this.$axios
          .post(`${window.$config.HOST}/infoManagement/addStyleGroup`,param)
          .then(response=>{
            if(response.data >= 0){
              this.$message({
                message: '',
                type: 'success'
              });
              this.handleSearch();
              this.ruleForm.tmpCustomer = "";
              this.ruleForm.tmpBrand = "";
              this.ruleForm.tmpRange = "";
              this.ruleForm.tmpStyleGroup="";
            }else{
              console.log("");
              this.$message.error('');
            }
          })
          .catch(error=>{
            console.log("");
            this.$message.error('');
          });
      }else if(this.controlData.ifStyleGroupChange){
        this.controlData.ifStyleGroupChange = false;
        var param = {
          id : this.ruleForm.id,
          name : (this.ruleForm.tmpStyleGroup==="")?null:this.ruleForm.tmpStyleGroup,
          // customerId : ((this.ruleForm.tmpCustomer === this.ruleForm.customerName)? this.ruleForm.customerId:this.ruleForm.tmpCustomer),
          // brandId : ((this.ruleForm.tmpBrand === this.ruleForm.brandName)? this.ruleForm.brandId:this.ruleForm.tmpBrand),
          rangeId :  ((this.ruleForm.tmpRange === this.ruleForm.rangeName)? this.ruleForm.rangeId:this.ruleForm.tmpRange),
          // clothingLevelId: this.ruleForm.clothingLevelName,
        };
        console.log(param);
        this.$axios
          .post(`${window.$config.HOST}/infoManagement/updateStyleGroup`,param)
          .then(response=>{
            var resData = response.data;
            if(resData >= 0){
              this.$message({
                message: '',
                type: 'success'
              });
              this.handleSearch();
            }else{
              this.$message.error('');
              console.log("");
            }
          })
          .catch(error=>{
            this.$message.error('');
          });
      }
      
      
      this.handleSearch();

      this.options.brandNameOptions = [];
      this.options.rangeNameOptions = [];
      this.ruleForm.tmpCustomer = "";
      this.ruleForm.tmpBrand = "";
      this.ruleForm.tmpRange = "";
      this.ruleForm.tmpStyleGroup = "";
      
      this.dialogFormVisible = false;
    },
    // 
    cancel(){
      const that = this;
      console.log("");
      this.ruleForm.tmpCustomer = "";
      this.ruleForm.tmpBrand = "";
      this.ruleForm.tmpRange = "";
      this.ruleForm.tmpStyleGroup="";
      /* that.$router.push({
        path: `/styleGroup/styleGroupManagement`,
      }); */
      this.dialogFormVisible = false;
    }
  }
}
</script>

<style lang="less" scoped>
.box-card {
  margin: 20px 50px;
  padding: 0 20px;
  .bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    .title {
      font-size: 14px;
      min-width: 75px;
      text-align: center;
    }
    .el-input {
      width: 300px;
      min-width: 75px;
    }
    .el-select {
      width: 300px;
      min-width: 75px;
    }
  }
  .block {
    padding: 30px 0;
    text-align: center;
  }
}
</style>


